<template>
  <v-card elevation="0" tile :color="bg_color" class="mx-5">
    <v-container class="py-0">
      <v-row justify="center">
        <v-card elevation="0" tile class="px-0" width="1200" :color="bg_color">
          <v-fab-transition>
            <v-btn
              :color="theme_color"
              dark
              absolute
              top
              left
              fab
              :x-large="!isSmall"
            >
              <v-icon color="white">{{ main_icon }}</v-icon>
            </v-btn>
          </v-fab-transition>
          <v-card-text>
            <v-list class="mb-10" :color="bg_color">
              <v-subheader class="px-0 mx-0 mt-5">
                <v-row justify="center" class="px-0 mx-0">
                  <v-col cols="4">
                    <span
                      :class="
                        isSmall
                          ? theme_text_color + ' subtitle-1'
                          : theme_text_color + ' title'
                      "
                    >
                      Publications
                    </span>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          icon
                          v-bind="attrs"
                          v-on="on"
                          class="mx-5"
                          @click="change_view"
                        >
                          <v-icon :color="theme_color" v-if="order_by_year">
                            mdi-sort-variant
                          </v-icon>
                          <v-icon :color="theme_color" v-else
                            >mdi-sort-variant-remove</v-icon
                          >
                        </v-btn>
                      </template>
                      <span v-if="order_by_year">Sort by author.</span>
                      <span v-else>Sort by year.</span>
                    </v-tooltip>
                  </v-col>
                  <v-spacer></v-spacer>
                </v-row>
              </v-subheader>
              <v-list-item-group active-class="border" :color="theme_color">
                <v-list-item
                  v-for="(item, index) of show_publication"
                  :key="index"
                  class="pa-0"
                  @click="search(item.title)"
                >
                  <v-row justify="center" class="px-0 mx-3">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <span
                          class="subtitle-1 font-weight-light"
                          v-html="index + 1 + '. ' + item.html"
                          v-bind="attrs"
                          v-on="on"
                        >
                        </span>
                      </template>
                      <span>Search Google Scholar.</span>
                    </v-tooltip>
                  </v-row>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card-text>
        </v-card>
      </v-row>
      <v-divider></v-divider>
    </v-container>
  </v-card>
</template>

